<template>
  <section class="banner">
    <div class="banner-content">
      <div class="w-full text-center mb-20 md:mb-0">
        <div 
          class="banner-title w-full xl:w-1/2 mx-auto"
          :class="{ 'opacity-0': transparent }">
          <span>Briefings on</span> 
          <h1>Securities, Financial and Corporate Regulatory </h1>
          <span>by</span>
          <h1>STEFANIE YUEN THIO</h1>
          <span>
            LL.B. (HONS) (NUS) <br>
            JOINT MANAGING PARTNER
          </span>
        </div>
        <!--  -->
        <div class="banner-desc bg-primary hidden md:block">
          <p 
            class="container w-full xl:w-2/3"
            :class="{ 'opacity-0': transparent }">
            Stefanie is an inveterate people person, and no one, neither the 
            cleaner in the same lift nor the CEO behind her in the coffee queue, 
            is spared her conversation. Her interest in people has not only built her a formidable 
            network of contacts, but also underpins her strategic abilities in boardroom battles.
          </p>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  data: () => ({
    transparent: false
  }),
  methods: {
    handleScroll() {
      if (window.scrollY > 200) {
        this.transparent = true
      } else {
        this.transparent = false        
      }
    }
  },  
  mounted () {
    window.addEventListener('scroll', this.handleScroll);
  },
  unmounted () {
    window.removeEventListener('scroll', this.handleScroll);
  }
}
</script>

<style scoped>
.banner{
  height: 85vh;
  background-image: url('../../assets/img/banner.jpg');
  @apply w-full bg-fixed bg-cover bg-center;
}
.banner-content{
  @apply h-full flex items-end text-white opacity-100 duration-700;
}
.banner-desc{
  @apply py-6 text-xl mt-4;
}
.banner-desc p{
  @apply opacity-100 duration-700;
}
h1{
  @apply text-3xl leading-tight;
}
@screen md{
  h1{
    @apply text-5xl;
  }
  .banner{
    height: 90vh;
  }
}
@screen xl{
  .banner{
    height: 100vh;
  }
  .banner-desc{
    @apply text-2xl;
  }
}
</style>